(function() {
  'use strict';
  angular.module('civic.pages')
    .controller('ReleasesController', ReleasesController);

  // @ngInject
  function ReleasesController($window, Releases, _) {
    console.log('ReleasesController loaded.');
    var vm = this;
    // TODO move releases init to ui-router state resolve
    Releases.initBase().then(function(releases){
      vm.releases = _.map(releases[0], function(release) {
        release.fileNames = _.map(release.files, function(filename) {
          return _.last(filename.split('/'));
        });
        release.fileUrls = _.map(release.files, function(filename) {
          return $window.location.origin + filename;
        });
        return release;
      });

    });
  }
})();

(function() {
  'use strict';
  angular.module('civic.common')
    .directive('title', titleDirective);

  // @ngInject
  /*jshint unused:false  */
  function titleDirective($rootScope, $timeout, TitleService) {
    var directive = {
      restrict: 'E',
      link: function() {
        var listener = function(event, data) {
          // console.log('title title:update listener called. ****************');
          $timeout(function() {
            $rootScope.title = (typeof data.newTitle !== 'undefined') ? 'CIViC: ' + data.newTitle : 'CIViC - Clinincal Interpretation of Variants in Cancer';
          });
        };
        $rootScope.$on('title:update', listener); // updates titles app-wide (e.g. 'subheader' directive)
      }
    };

    return directive;
  }
})();

(function () {
  'use strict';
  angular.module('civic.common')
    .directive('subheader', subheader)
    .controller('SubheaderCtrl', SubheaderCtrl);

  // @ngInject
  function subheader(Security) {
    var directive = {
      restrict: 'E',
      scope: true,
      templateUrl: 'components/directives/subheader.tpl.html',
      controller: SubheaderCtrl,
      link: /* @ngInject */ function($scope) {
        $scope.isAuthenticated = Security.isAuthenticated;
        $scope.isEditor = Security.isAuthenticated;
      }
    };
    return directive;
  }

  // @ngInject
  function SubheaderCtrl($scope, $rootScope, _, Security) {
    $scope.isAuthenticated = Security.isAuthenticated;
    $scope.isEditor = Security.isAuthenticated;

    $scope.$rootScope = $rootScope;

    $scope.addEvidenceUrlBase = $scope.addEvidenceUrl = '#/add/evidence/basic';

    $scope.$watchCollection('$rootScope.stateParams', function(stateParams){
      if(_.has(stateParams, 'geneId')) {
        $scope.addEvidenceUrl = $scope.addEvidenceUrlBase + '?geneId=' + stateParams.geneId;

        if(_.has(stateParams, 'variantId')) {
          $scope.addEvidenceUrl = $scope.addEvidenceUrl + '&variantId=' + stateParams.variantId;
        }
      }
    });

    $rootScope.$on('title:update', function(event, data) {
      $scope.view.stateTitle = data.newTitle;
    });
  }
})();

