  $templateCache.put('app/pages/help_variant_group_overview.tpl.html',
    '<div class="help-sub-tab"><p>Variant groups can associate multiple related variants within or between genes. Each variant can belong to one or more variant groups, these act to combine functionally similar or clinically related variants into a single entity. For example, \'FGFR Fusions\' is a group that contains several gene fusions where FGFR2 (or FGFR3) is involved in a fusion with various 3\' partner genes, and \'Imatinib Resistance Mutations\' is a group of variants that confer resistance to imatinib treatment. A variant group may also contain variants from multiple genes. For example, the group \'EGFR TKI Resistance\' consists of variants in EGFR, MET, and KRAS.</p><h4>Variant Group Attributes</h4><table class="table table-striped table-condensed table-bordered"><tr><th class="attribute">Attribute</th><th class="description">Description</th><th class="source">Source</th><th class="editable">Editable</th></tr><tr><td class="attribute">Name</td><td class="description">Name of the Variant Group</td><td class="source">CIViC</td><td class="editable">Yes</td></tr><tr><td class="attribute">Description</td><td class="description">User-defined summary of the clinical relevance of this variant group.</td><td class="source">CIViC</td><td class="editable">Yes</td></tr><tr><td class="attribute">Variants</td><td class="description">User-defined list of variants in the variant group.</td><td class="source">CIViC</td><td class="editable">Yes</td></tr></table></div>');
