module.run(['$templateCache', function($templateCache) {
  $templateCache.put('app/pages/home.tpl.html',
    '<div class="home-view"><div class="row"><div class="col-xs-12 col-sm-6 discover"><div class="row"><div class="col-sm-12 col-md-6 icon"><a ui-sref="browse.variants"><img src="assets/images/icon_discover.svg" alt="Discover" class="img-responsive"></a></div><div class="col-sm-12 col-md-6 blurb"><p>Discover supported clinical intepretations of mutations related to cancer.</p></div></div></div><div class="col-xs-12 col-sm-6 collaborate"><div class="row"><div class="col-sm-12 col-md-6 icon"><a ui-sref="collaborate"><img src="assets/images/icon_collaborate.svg" alt="Discover" class="img-responsive" width="90%"></a></div><div class="col-sm-12 col-md-6 blurb"><p>Collaborate with colleagues to add variants and support for cancer-related mutations.</p></div></div></div></div><div class="row" style="margin-top: 16px;"><div class="col-xs-12 intro-block"><div class="row"><div class="col-xs-6"><div class="intro"><h4>The Precision Medicine Revolution</h4><p>Precision medicine refers to the use of prevention and treatment strategies that are tailored to the unique features of each individual and their disease. In the context of cancer this might involve the identification of specific mutations shown to predict response to a targeted therapy. The biomedical literature describing these associations is large and growing rapidly. Currently these interpretations exist largely in private or encumbered databases resulting in extensive repetition of effort.</p></div></div><div class="col-xs-6"><div class="intro"><h4>CIViC\'s Role in Precision Medicine</h4><p>Realizing precision medicine will require this information to be centralized, debated and interpreted for application in the clinic. <strong>CIViC is an open access, open source, community-driven web resource for Clinical Interpretation of Variants in Cancer.</strong> Our goal is to enable precision medicine by providing an educational forum for dissemination of knowledge and active discussion of the clinical significance of cancer genome alterations.</p></div></div></div></div></div><div class="row"><div class="col-xs-12 col-sm-6 stats-block"><div class="row"><div class="col-xs-12"><h4>Database and Community Statistics</h4></div></div><div class="row"><stats-block stats="vm.stats.genes" title="Genes" icon-url="" cols="col-xs-4"></stats-block><stats-block stats="vm.stats.variants" title="Variants" icon-url="" cols="col-xs-4"></stats-block><stats-block stats="vm.stats.evidence_items" title="Evidence Items" icon-url="" cols="col-xs-4"></stats-block></div><div class="row" style="margin-top: 16px;"><stats-block stats="vm.stats.drugs" title="Drugs" icon-url="" cols="col-xs-4"></stats-block><stats-block stats="vm.stats.diseases" title="Diseases" icon-url="" cols="col-xs-4"></stats-block><stats-block stats="vm.stats.sources" title="Publications" icon-url="" cols="col-xs-4"></stats-block></div><div class="row" style="margin-top: 16px;"><stats-block stats="vm.stats.users" title="Users" icon-url="" cols="col-xs-6"></stats-block><stats-block stats="vm.stats.comments" title="Comments" icon-url="" cols="col-xs-6"></stats-block></div>--></div><div class="col-xs-12 col-sm-6 stats-block"><div class="row"><div class="col-xs-12"><h4>CIViC Activity</h4></div></div><div class="row"><div class="col-xs-12"><ul class="events"><li ng-repeat="event in vm.events | limitTo:6"><event-item event="event" theme="\'dark\'">EVENT</event-item></li><li><a class="btn btn-default btn-xs btn-block" ui-sref="activity">View All Activity&nbsp;<i class="glyphicon glyphicon-arrow-right"></i></a></li></ul></div></div></div></div><div class="row" style="margin-top: 32px;"><div class="col-xs-12 disclaimer-block"><div class="disclaimer small"><p><strong>Medical Disclaimer: This resource is intended for purely research purposes. It should not be used for emergencies or medical or professional advice.</strong></p><p>CIViC is a community-edited forum for discussion and interpretation of peer-reviewed publications pertaining to the clinical relevance of variants (or biomarker alterations) in cancer. These interpretations may include associations between molecular alterations (or lack of alteration) and one or more drugs, diagnoses, prognoses or other treatment decisions. These interpretations of clinical significance (or lack of clinical significance) are purely for research purposes. A finding of no interpretation does not necessarily indicate lack of relevance for any specific variant or biomarker alteration. Interpretations are not presented in ranked order of potential or predicted importance.These interpretations make no promise or guarantee of any clinical benefit (or lack of clinical benefit).</p><p>The civicdb.org (civic.genome.wustl.edu) website does not provide any medical or healthcare products, services or advice, and is not for medical emergencies or urgent situations. <strong>IF YOU THINK YOU MAY HAVE A MEDICAL EMERGENCY, CALL YOUR DOCTOR OR 911 IMMEDIATELY</strong>. Information contained on this website is not a substitute for a doctor\'s medical judgment or advice. We recommend that you discuss your specific, individual health concerns with your doctor or health care professional.</p></div></div></div></div>');
}]);
